<template>
  <el-card shadow="hover" class="notice" :bodyStyle="{padding: '0', height: '240px'}">
    <div slot="header">公告</div>
    <ul v-infinite-scroll="loadNotice" class="notice-scroll">
      <li v-for="i in notices" :key="i.id" @click="showNotice(i)">
        <el-tag v-if="i.type==='top'" size="mini">置顶</el-tag>
        {{ i.title }}
      </li>
      <p class="no-more" v-if="noMore">没有更多了</p>
    </ul>
    <el-dialog width="600px" :visible.sync="isShow" :center="true">
      <div slot="title">{{ currentNotice.title }}</div>
      <div>
        {{ currentNotice.content }}
      </div>
    </el-dialog>
  </el-card>
</template>

<script>
export default {
  name: 'NoticePC',
  data () {
    return {
      notices: [],
      page: 1,
      noMore: false,
      isShow: false,
      currentNotice: {}
    }
  },
  methods: {
    loadNotice () {
      if (this.noMore) return
      this.$axios.get('/api/v1/notice/?page=' + this.page)
        .then((resp) => {
          if (resp.data.next === null) {
            this.noMore = true
          }
          this.notices = this.notices.concat(resp.data.results)
          this.page += 1
        })
    },
    showNotice (notice) {
      this.isShow = true
      this.$axios.get('/api/v1/notice/' + notice.id + '/')
        .then((resp) => {
          this.currentNotice = resp.data
        })
    }
  }
}
</script>

<style scoped>
.notice {
  height: 300px;
  width: 300px;
  display: flex;
  flex-direction: column;
}

.notice-scroll {
  overflow-x: hidden;
  overflow-y: scroll;
  height: 100%;
}

.notice-scroll::-webkit-scrollbar {
  width: 3px;
  /*display: none;*/
}

.notice-scroll::-webkit-scrollbar-track, .notice-scroll::-webkit-scrollbar-track-piece, .notice-scroll::-webkit-scrollbar-button {
  display: none;
}

.notice-scroll::-webkit-scrollbar-thumb {
  background-color: rgba(144, 147, 153, 0.3);
  border-radius: 20px;
  transition: background-color 0.3s;
}

.notice-scroll li {
  height: 40px;
  line-height: 40px;
  vertical-align: center;
  padding: 0px 12px;
  position: relative;
  transition: 0.8s;
}

.notice-scroll li:hover {
  background-color: #E3E5E7;
}

.notice-scroll li:after {
  display: block;
  content: '';
  position: absolute;
  height: 1px;
  width: 276px;
  margin: 0 auto;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #6c757d4d;
  /*border-bottom: 1px #6c757d4d solid;*/
}

.notice-scroll li:last-of-type:after {
  display: none;
}

.no-more {
  color: gray;
  font-size: 12px;
  text-align: center;
}
</style>



